import React from "react";

export function Flex3() {
  return (
    <>
      <h2 className="inner-title">align-self</h2>
      <span className="description">
        description : align-self default flex-start
      </span>
      <span className="description">
        1 default  , 2 align-self: center, 3 align-self:
        flex-end
      </span>
      <div className="wrapper-3">
        <div className="box box3">
          <span className="box-number">1</span>
        </div>
        <div className="box box3">
          <span className="box-number">2</span>
        </div>
        <div className="box box3">
          <span className="box-number">3</span>
        </div>
      </div>
    </>
  );
}
