import React from "react";

export function Flex4() {
  return (
    <>
      <h2 className="inner-title">flex-wrap</h2>
      <span className="description">
        description: flex-wrap box   .
      </span>
      <span className="description">
        justify-content space-around  flex-wrap:nowrap  box
           
      </span>
      <span className="description">
        flex-wrap: wrap       .
      </span>
      <div className="wrapper-4">
        <div className="box">
          <span className="box-number">1</span>
        </div>
        <div className="box">
          <span className="box-number">2</span>
        </div>
        <div className="box">
          <span className="box-number">3</span>
        </div>
        <div className="box">
          <span className="box-number">4</span>
        </div>
        <div className="box">
          <span className="box-number">5</span>
        </div>
        <div className="box">
          <span className="box-number">6</span>
        </div>
      </div>
    </>
  );
}
